.inner {
	max-width: 1000px;
	margin: 0 auto;
	padding: 0 10px;
	width: 100%; }

body {
	font-size: $base;
	font-family: "PT Sans";
	font-weight: 400;
	line-height: 1.6;
	min-width: 320px; }

.slick-next,
.slick-prev {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 50;
	cursor: pointer;
	font-size: 0;
	width: 34px;
	height: 60px;
	&:after,
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		transform: translateY(-50%); } }
.slick-prev {
	&:before,
	&:after {
		margin-left: -9px; }
	&:after {
		left: 60%;
		@include arr(18, 34, $gray, l); }
	&:before {
		left: 50%;
		@include arr(18, 34, $black, l); } }
.slick-next {
	&:before,
	&:after {
		margin-right: -9px; }
	&:after {
		right: 60%;
		@include arr(18, 34, $gray, r); }
	&:before {
		right: 50%;
		@include arr(18, 34, $black, r); } }

.slick-next {
	right: 10px; }
.slick-prev {
	left: 10px; }
.slider {
	position: relative;
	overflow: hidden;
	&__container {
		margin-top: -40px; }
	&__inner {
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; }
	.item {
		&__img {
			text-align: center;
			img {
				display: inline-block; } } } }
