  font-weight: 300;
  src: local("OpenSansCondensed-Light"), local("OpenSansCondensed-Light"), url("../fonts/OpenSansCondensed-Light.woff2") format("woff2"), url("../fonts/OpenSansCondensed-Light.woff") format("woff");
}

@font-face {
  font-family: "Open Sans Condensed";
  font-style: italic;
  font-weight: 300;
  src: local("OpenSansCondensed-Lightitalic"), local("OpenSansCondensed-Lightitalic"), url("../fonts/OpenSansCondensed-Lightitalic.woff2") format("woff2"), url("../fonts/OpenSansCondensed-Lightitalic.woff") format("woff");
}

