.inner {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
}

body {
  font-size: 14px;
  font-family: "PT Sans";
  font-weight: 400;
  line-height: 1.6;
  min-width: 320px;
}

.slick-next,
.slick-prev {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 50;
  cursor: pointer;
  font-size: 0;
  width: 34px;
  height: 60px;
}

.slick-next:after, .slick-next:before,
.slick-prev:after,
.slick-prev:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slick-prev:before, .slick-prev:after {
  margin-left: -9px;
}

.slick-prev:after {
  left: 60%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 18px 17px 0;
  border-color: transparent #dcdcdc transparent transparent;
}

.slick-prev:before {
  left: 50%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 18px 17px 0;
  border-color: transparent #000 transparent transparent;
}

.slick-next:before, .slick-next:after {
  margin-right: -9px;
}

.slick-next:after {
  right: 60%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 0 17px 18px;
  border-color: transparent transparent transparent #dcdcdc;
}

.slick-next:before {
  right: 50%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 17px 0 17px 18px;
  border-color: transparent transparent transparent #000;
}

.slick-next {
  right: 10px;
}

.slick-prev {
  left: 10px;
}

.slider {
  position: relative;
  overflow: hidden;
}

.slider__container {
  margin-top: -40px;
}

.slider__inner {
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.slider .item__img {
  text-align: center;
}

.slider .item__img img {
  display: inline-block;
}

.sidebar {
  grid-area: sidebar;
}

.main {
  grid-template-areas: "breadcrumbs breadcrumbs" "sidebar content";
  grid-template-columns: 185px 1fr;
  display: grid;
  grid-column-gap: 20px;
}

@media only screen and (max-width: 1023px) {
  .main {
    grid-template-areas: "breadcrumbs" "sidebar" "content";
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
  }
}

.content {
  grid-area: content;
}

.text {
  font-size: 0;
  margin-bottom: 200px;
}

@media only screen and (max-width: 1023px) {
  .text {
    margin-bottom: 120px;
  }
}

@media only screen and (max-width: 767px) {
  .text {
    margin-bottom: 80px;
  }
}

@media only screen and (max-width: 599px) {
  .text {
    margin-bottom: 0;
  }
}

.text__row {
  margin-left: -20px;
  margin-right: -20px;
}

@media only screen and (max-width: 1023px) {
  .text__row {
    margin-left: 0;
    margin-right: 0;
  }
}

.text__col {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  padding: 0 20px;
}

@media only screen and (max-width: 1023px) {
  .text__col {
    width: 100%;
    padding: 20px 0 0 0;
  }
}

.text__col p {
  color: #767676;
  font-size: 14px;
}

.products {
  margin-bottom: 20px;
  font-size: 0;
  display: grid;
  grid-gap: 20px 14px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
}

@media only screen and (max-width: 767px) {
  .products {
    padding-left: 0;
  }
}

@media only screen and (max-width: 480px) {
  .products {
    margin-bottom: 10px;
  }
}

.products_second {
  grid-area: products_second;
}

.products .product__img {
  display: block;
  text-decoration: none;
}

.products .product__img img {
  max-width: 100%;
  height: auto;
  width: 100%;
}

.products .product__inner {
  background-color: #dcdcdc;
  padding: 20px;
}

.products .product__text {
  font-size: 18px;
  color: #767676;
  font-weight: 400;
  line-height: 1;
  display: block;
  text-decoration: none;
  height: 3em;
  overflow: hidden;
  margin-top: 20px;
}

.products .product__text:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 599px) {
  .products .product__text {
    font-size: 14px;
  }
}

.products .product__controls {
  margin-top: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.products .product__price {
  font-size: 28px;
  color: #444444;
  line-height: 1;
  letter-spacing: 0.025em;
  font-weight: 700;
  text-decoration: none;
}

@media only screen and (max-width: 599px) {
  .products .product__price {
    font-size: 22px;
  }
}

@media only screen and (max-width: 480px) {
  .products .product__price {
    font-size: 18px;
  }
}

.products .product__basket {
  margin-right: 18px;
  text-decoration: none;
  cursor: pointer;
}

@media only screen and (max-width: 668px) {
  .products .product__basket {
    margin-right: 0;
  }
}

.products .product__basket-icon {
  display: block;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

