    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.sidebar_logo {
  font-size: 0;
  text-align: center;
  margin-top: 110px;
  display: inline-block;
  text-decoration: none;
}

@media only screen and (max-width: 1023px) {
