    margin-top: 0;
  }
}

.sidebar_logo-icon {
  display: block;
  margin: 0 auto;
}

.sidebar_logo-title {
  font-size: 30px;
  letter-spacing: 0.025em;
  line-height: 0.83;
  color: #444444;
  text-decoration: none;
  font-weight: 700;
  margin-top: 5px;
  display: block;
}

@media only screen and (max-width: 767px) {
  .menu_sidebar {
    display: inline-block;
  }
}

.menu_sidebar .menu__sublink {
  padding-left: 20px;
  position: relative;
}

.menu_sidebar .menu__sublink:before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 5px;
  height: 5px;
  background-color: #767676;
  border-radius: 50px;
}

.menu_sidebar .menu__sublink:not(:last-child) {
  margin-bottom: 5px;
}

.menu_sidebar .menu__link {
  display: block;
  text-decoration: none;
  line-height: 30px;
  letter-spacing: 0.025em;
  color: #767676;
  font-size: 18px;
  transition: color .24s ease-in-out;
}

.menu_sidebar .menu__link:hover {
  text-decoration: underline;
}

.menu_sidebar .menu__item {
  display: block;
}

.menu_sidebar .menu__item.is-active {
  text-decoration: underline;
}

.menu {
  font-size: 0;
}

.menu__item {
  display: inline-block;
}

.menu__link {
  text-decoration: none;
}

.menu__sublink {
  display: block;
  text-decoration: none;
  line-height: 30px;
  letter-spacing: 0.025em;
  color: #767676;
  font-size: 14px;
  transition: color .24s ease-in-out;
}

.menu__sublink.is-active {
  text-decoration: underline;
}

.menu__sublink.is-active, .menu__sublink:hover {
  color: #000;
  text-decoration: underline;
}

.menu.menu_nav {
  background-color: #777777;
  text-align: center;
}

@media only screen and (max-width: 599px) {
  .menu.menu_nav .menu__item {
    display: block;
  }
}

.menu.menu_nav .menu__item:not(:last-child) .menu__link:after {
  content: '';
  display: block;
  position: absolute;
  width: 3px;
  height: 25px;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #fff;
}

@media only screen and (max-width: 599px) {
  .menu.menu_nav .menu__item:not(:last-child) .menu__link:after {
    display: none;
  }
}

.menu.menu_nav .menu__link {
  font-size: 30px;
  color: #fff;
  font-weight: 400;
  padding: 26px 20px;
  position: relative;
  display: inline-block;
  vertical-align: top;
  line-height: 1;
}

@media only screen and (max-width: 1023px) {
  .menu.menu_nav .menu__link {
    font-size: 24px;
  }
}

@media only screen and (max-width: 992px) {
  .menu.menu_nav .menu__link {
    font-size: 18px;
  }
}

@media only screen and (max-width: 767px) {
  .menu.menu_nav .menu__link {
    padding: 18px 14px;
  }
}

@media only screen and (max-width: 599px) {
  .menu.menu_nav .menu__link {
    display: block;
    border-bottom: 1px solid #dcdcdc;
  }
}

.menu.menu_nav .menu__link:hover {
  text-decoration: underline;
}

.menu.menu_footer .menu__item {
  display: block;
}

.menu.menu_footer .menu__item:not(:last-child) {
