}

.menu.menu_footer .menu__link {
  text-decoration: underline;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  position: relative;
  display: inline-block;
  vertical-align: top;
  line-height: 1.2;
}

.menu.menu_footer .menu__link:hover {
