}

.breadcrumbs {
  font-size: 0;
  padding: 10px 0;
  margin-bottom: 20px;
  grid-area: breadcrumbs;
}

@media only screen and (max-width: 767px) {
  .breadcrumbs {
    display: none;
  }
}

.breadcrumbs__item {
  display: inline-block;
}

.breadcrumbs__item:last-child .breadcrumbs__link {
  text-decoration: none;
  cursor: default;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 26px;
  position: relative;
}

.breadcrumbs__item:not(:last-child):after {
  content: '/';
  display: block;
  position: absolute;
  top: 50%;
  font-size: 14px;
  transform: translateY(-50%);
  right: -16px;
  color: #767676;
}

.breadcrumbs__link {
  text-decoration: underline;
  cursor: pointer;
  display: block;
  font-size: 14px;
  color: #767676;
  letter-spacing: 0.025em;
}

.pagination {
  padding-bottom: 65px;
