}

@media only screen and (max-width: 599px) {
  .pagination {
    padding-bottom: 40px;
  }
}

@media only screen and (max-width: 480px) {
  .pagination {
    padding-bottom: 20px;
  }
}

.pagination__item {
  display: inline-block;
  margin: 0 10px;
}

.pagination__link {
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;
  line-height: 22px;
  display: block;
  color: #767676;
}

.pagination__link.is-current {
  font-size: 18px;
  color: #777777;
  font-weight: 400;
}
