.pagination {
	padding-bottom: 65px;
	margin-bottom: 30px;
	border-bottom: 1px solid $grayLink;
	font-size: 0;
	text-align: center;
	@include md {
		margin-bottom: 10px; }
	@include xs {
		padding-bottom: 40px; }
	@include r(480) {
		padding-bottom: 20px; }
	&__item {
		display: inline-block;
		margin: 0 10px; }
	&__link {
		text-decoration: none;
		cursor: pointer;
		font-size: $base;
		line-height: 22px;
		display: block;
		color: $grayLink;
		&.is-current {
			font-size: $font-md;
			color: $grayDark;
			font-weight: 400; } } }
