.menu {
	font-size: 0;
	&__item {
		display: inline-block; }
	&__link {
		text-decoration: none; }
	&__sublink {
		display: block;
		text-decoration: none;
		line-height: 30px;
		letter-spacing: 0.025em;
		color: $grayLink;
		font-size: $base;
		transition: color .24s ease-in-out; }
	&__sublink.is-active {
		text-decoration: underline; }
	&__sublink.is-active,
	&__sublink:hover {
		color: $black;
		text-decoration: underline; }
	&.menu_nav {
		background-color: $grayDark;
		text-align: center;
		.menu__item {
			@include xs {
				display: block; }
			&:not(:last-child) {
				.menu__link:after {
					content: '';
					display: block;
					position: absolute;
					width: 3px;
					height: 25px;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
					background-color: $white;
					@include xs {
						display: none; } } } }
		.menu__link {
			font-size: 30px;
			color: $white;
			font-weight: 400;
			padding: 26px 20px;
			position: relative;
			display: inline-block;
			vertical-align: top;
			line-height: 1;
			@include md {
				font-size: 24px; }
			@include r(992) {
				font-size: 18px; }
			@include sm {
				padding: 18px 14px; }
			@include xs {
				display: block;
				border-bottom: 1px solid $gray; } }
		.menu__link:hover {
			text-decoration: underline; } }
	&.menu_footer {
			.menu__item {
				display: block;
				&:not(:last-child) {
					margin-bottom: 10px; } }
			.menu__link {
				text-decoration: underline;
				font-size: $base;
				color: $white;
				font-weight: 400;
				position: relative;
				display: inline-block;
				vertical-align: top;
				line-height: 1.2;
				&:hover {
					text-decoration: none; } } } }
