//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

@font-face {
	@include fontdefault(normal, 700, $opensans);
	@include fontpath('OpenSansCondensed-Bold', 'OpenSansCondensed-Bold', 'OpenSansCondensed-Bold'); }

@font-face {
	@include fontdefault(normal, 300, $opensans);
	@include fontpath('OpenSansCondensed-Light', 'OpenSansCondensed-Light', 'OpenSansCondensed-Light'); }

@font-face {
	@include fontdefault(italic, 300, $opensans);
	@include fontpath('OpenSansCondensed-Lightitalic', 'OpenSansCondensed-Lightitalic', 'OpenSansCondensed-Lightitalic'); }

